.room-code{
    height: 40px;
    border-radius: 8px;
    overflow: hidden;

    padding:0;

    background: #fff;
    border: 1px solid #fd5a5a;
    cursor: pointer;

    display: flex;
    

    div{
        background: #fd5a5a;
        padding: 0 12px;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        width: auto;
        
    }
    span{
        display: block;
        align-self: center;
        flex: 1;
        padding: 0 16px 0 12px;
        width: 240px;
        font-size: 14px;
        font-weight: 500;

        overflow-x: hidden;

    }
}