.question{
    background: #fefefe;
    border-radius: 8px;
    box-shadow: 0 2px 12px rgba($color: #000000, $alpha: 0.04);
    padding: 24px;

    & + .question {
        margin-top: 8px;

    }
    &.highlighted{
        background: #f4f0ff;
        border: 1px solid #fd5a5a;

        footer .user-info span{
          color:#29292e
        }

    }

    &.answered{
        background: #DBDCDD;
        border: 0;
    }

    p{
        color: #29292e;
    }
    footer{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 24px;

        .user-info{
            display: flex;
            align-items: center;

            img{
                width: 35px;
                height: 35px;
                border-radius: 50%;
            }
            > span{
                margin-left: 8px;
                color: #737380;
                
                font-size: 14px;
            }
        }

        button{
            border:0;
            background: transparent;
            cursor: pointer;
            transition: filter 0.2s;

            &.like-button{
                display: flex;
                align-items: flex-end;
                color: #737380;

                gap: 8px;

                &.liked{
                    color: #fd5a5a;

                    svg path{
                        stroke: #fd5a5a;
                    }
                }



            }
            &:hover{
                filter:brightness(0.7)
            }

        }
    }
}